import { createBrowserRouter } from "react-router-dom";
import Dashboard from "../pages/Dashboard";
import Login from "../pages/Login";
import ResourceList from "../pages/resource/Index";
import ResourceCreate from "../pages/resource/Create";
import ResourceView, { Loader as ResourceLoader } from "../pages/resource/View";
import ResourceEdit from "../pages/resource/Edit";
import CompanyList from "../pages/company/Index";
import ProjectList from "../pages/project/Index";
import ProjectCreate from "../pages/project/Create";
import ProjectView, { Loader as ProjectLoader } from "../pages/project/View";
import ProjectEdit from "../pages/project/Edit";
import Expense from "../pages/Expense";
import { AuthenticatedRoute, GuestRoute } from "./AuthMiddelware";

export const router = createBrowserRouter([
    {
        path: "/",
        element: <AuthenticatedRoute element={<Dashboard />} />,
    },
    {
        path: "/resources",
        element: <AuthenticatedRoute element={<ResourceList />} />,
    },
    {
        path: "/resources/create",
        element: <AuthenticatedRoute element={<ResourceCreate />} />,
    },
    {
        path: "/resources/:id",
        element: <AuthenticatedRoute element={<ResourceView />} />,
        loader: ResourceLoader,
    },
    {
        path: "/resources/:id/edit",
        element: <AuthenticatedRoute element={<ResourceEdit />} />,
        loader: ResourceLoader,
    },
    {
        path: "/companies",
        element: <AuthenticatedRoute element={<CompanyList />} />,
    },
    {
        path: "/projects",
        element: <AuthenticatedRoute element={<ProjectList />} />,
    },
    {
        path: "/projects/create",
        element: <AuthenticatedRoute element={<ProjectCreate />} />,
    },
    {
        path: "/projects/:id",
        element: <AuthenticatedRoute element={<ProjectView />} />,
        loader: ProjectLoader,
    },
    {
        path: "/projects/:id/edit",
        element: <AuthenticatedRoute element={<ProjectEdit />} />,
        loader: ProjectLoader,
    },
    {
        path: "/expenses",
        element: <AuthenticatedRoute element={<Expense />} />,
    },
    {
        path: "/login",
        element: <GuestRoute element={<Login />} />,
    },
]);
