import React, { useState } from "react";
import MainLayout from "../../components/layouts/MainLayout";
import ContentHeader from "../../components/ContentHeader";
import { Link, useLoaderData } from "react-router-dom";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Card from "react-bootstrap/Card";

export default function ResourceEdit() {
    const { response }: any = useLoaderData();

    const [formData, setFormData] = useState({
        name: response.name,
        email: response.email,
        contact_number: response.contact_number,
        employee_number: response.employee_number,
        designation: 2,
        salaries: response.salaries,
    });

    const handleChange = (e: any) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const handleSalaryChange = (e: any, index: number) => {
        const { name, value } = e.target;

        setFormData((prevData) => {
            const newSalaries = [...prevData.salaries];
            newSalaries[index][name] = value;
            return { ...prevData, salaries: newSalaries };
        });
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        console.log(formData);
    };

    return (
        <MainLayout>
            <ContentHeader title="Edit Resource">
                <Link className="btn btn-primary" to="/resources">
                    <i className="ti ti-list h5 text-white" />
                </Link>
                <Link className="btn btn-primary" to="/resources/create">
                    <i className="ti ti-plus h5 text-white" />
                </Link>
                <Link className="btn btn-primary" to={"/resources/"+response.id}>
                    <i className="ti ti-eye h5 text-white" />
                </Link>
            </ContentHeader>
            <Card>
                <Card.Body>
                    <Form method="post" onSubmit={handleSubmit}>
                        <Form.Group
                            as={Row}
                            className="mb-3"
                            controlId="formHorizontalName"
                        >
                            <Form.Label column sm={2}>
                                Name
                            </Form.Label>
                            <Col sm={10}>
                                <Form.Control
                                    type="text"
                                    onChange={handleChange}
                                    name="name"
                                    value={formData.name}
                                    placeholder="Enter Name..."
                                />
                            </Col>
                        </Form.Group>

                        <Form.Group
                            as={Row}
                            className="mb-3"
                            controlId="formHorizontalEmail"
                        >
                            <Form.Label column sm={2}>
                                Email
                            </Form.Label>
                            <Col sm={10}>
                                <Form.Control
                                    type="email"
                                    name="email"
                                    onChange={handleChange}
                                    value={formData.email}
                                    placeholder="Enter Email..."
                                />
                            </Col>
                        </Form.Group>

                        <Form.Group
                            as={Row}
                            className="mb-3"
                            controlId="formHorizontalContactNumber"
                        >
                            <Form.Label column sm={2}>
                                Contact Number
                            </Form.Label>
                            <Col sm={10}>
                                <Form.Control
                                    type="text"
                                    name="contact_number"
                                    onChange={handleChange}
                                    value={formData.contact_number}
                                    placeholder="Enter Contact Number..."
                                />
                            </Col>
                        </Form.Group>

                        <Form.Group
                            as={Row}
                            className="mb-3"
                            controlId="formHorizontalEmployeeNumber"
                        >
                            <Form.Label column sm={2}>
                                Employee Number
                            </Form.Label>
                            <Col sm={10}>
                                <Form.Control
                                    type="text"
                                    name="employee_number"
                                    onChange={handleChange}
                                    value={formData.employee_number}
                                    placeholder="Enter Employee Number..."
                                />
                            </Col>
                        </Form.Group>

                        <Form.Group
                            as={Row}
                            className="mb-3"
                            controlId="formHorizontalDesignation"
                        >
                            <Form.Label column sm={2}>
                                Designation
                            </Form.Label>
                            <Col sm={10}>
                                <Form.Select
                                    name="designation"
                                    defaultValue={formData.designation}
                                    onChange={handleChange}
                                    aria-label="Default select example"
                                >
                                    <option value="">Select Designation</option>
                                    <option value="1">One</option>
                                    <option value="2">Two</option>
                                    <option value="3">Three</option>
                                </Form.Select>
                            </Col>
                        </Form.Group>

                        <Form.Group as={Row} className="mb-3 mt-5">
                            <Col>
                                <Button type="submit" className="w-100">
                                    Update
                                </Button>
                            </Col>
                        </Form.Group>
                    </Form>
                </Card.Body>
            </Card>

            <Card>
                <Card.Body>
                    <Card.Title>Salaries</Card.Title>

                    <hr />

                    {response.salaries.map((salary: any, index: number) => (
                        <React.Fragment key={index}>
                            <Form.Group
                                as={Row}
                                className="mb-3"
                                controlId={"formHorizontalAmount-" + index}
                            >
                                <Form.Label column sm={2}>
                                    Amount
                                </Form.Label>
                                <Col sm={10}>
                                    <Form.Control
                                        type="text"
                                        name="amount"
                                        onChange={(e) =>
                                            handleSalaryChange(e, index)
                                        }
                                        value={salary.amount}
                                        placeholder="Enter Salary Amount..."
                                    />
                                </Col>
                            </Form.Group>

                            <Form.Group
                                as={Row}
                                className="mb-3"
                                controlId={"formHorizontalStartDate" + index}
                            >
                                <Form.Label column sm={2}>
                                    Start Date
                                </Form.Label>
                                <Col sm={10}>
                                    <Form.Control
                                        type="date"
                                        name="start_date"
                                        onChange={(e) =>
                                            handleSalaryChange(e, index)
                                        }
                                        value={salary.start_date}
                                        placeholder="Start Date"
                                    />
                                </Col>
                            </Form.Group>

                            <Form.Group
                                as={Row}
                                className="mb-3"
                                controlId={"formHorizontalEndDate" + index}
                            >
                                <Form.Label column sm={2}>
                                    End Date
                                </Form.Label>
                                <Col sm={10}>
                                    <Form.Control
                                        type="date"
                                        name="end_date"
                                        onChange={(e) =>
                                            handleSalaryChange(e, index)
                                        }
                                        value={salary.end_date}
                                        placeholder="End Date"
                                    />
                                </Col>
                            </Form.Group>

                            <Form.Group as={Row} className="mb-3 mt-5">
                                <Col>
                                    <Button type="submit" className="w-100">
                                        Update
                                    </Button>
                                </Col>
                            </Form.Group>

                            <hr />
                        </React.Fragment>
                    ))}
                </Card.Body>
            </Card>
        </MainLayout>
    );
}
