import { useState } from "react";
import MainLayout from "../../components/layouts/MainLayout";
import ContentHeader from "../../components/ContentHeader";
import Row from "react-bootstrap/Row";
import { Table } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Form from "react-bootstrap/Form";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";

export default function CompanyList() {
    const initialFormData = {
        name: "",
        country: "",
        contact_name: "",
        contact_email: "",
        is_active: "",
    };

    const listing = [
        {
            name: "Google",
            country: "USA",
            contact_name: "Lary Page",
            contact_email: "google@mail.com",
            is_active: true,
        },
        {
            name: "Axis Bank",
            country: "India",
            contact_name: "Deepak",
            contact_email: "axisbank@mail.com",
            is_active: true,
        },
        {
            name: "Bajaj Auto",
            country: "India",
            contact_name: "Gopal",
            contact_email: "BajajAuto@mail.com",
            is_active: true,
        },
        {
            name: "ACG Group",
            country: "India",
            contact_name: "Rupesh",
            contact_email: "ACGGroup@mail.com",
            is_active: false,
        },
        {
            name: "Ador Group",
            country: "India",
            contact_name: "Jay",
            contact_email: "Ador Group@mail.com",
            is_active: true,
        },
        {
            name: "Angel One",
            country: "India",
            contact_name: "Gaurav",
            contact_email: "AngelOne@mail.com",
            is_active: true,
        },
        {
            name: "Arvind",
            country: "India",
            contact_name: "Vivek",
            contact_email: "Arvind@mail.com",
            is_active: true,
        },
        {
            name: "Ambuja Cements",
            country: "India",
            contact_name: "Darshan",
            contact_email: "AmbujaCements@mail.com",
            is_active: true,
        },
        {
            name: "Adani Group",
            country: "India",
            contact_name: "Suketu",
            contact_email: "AdaniGroup@mail.com",
            is_active: true,
        },
        {
            name: "Aditya Birla Group",
            country: "India",
            contact_name: "Nasir",
            contact_email: "AdityaBirlaGroup@mail.com",
            is_active: false,
        },
    ];

    const [formData, setFormData] = useState(initialFormData);

    const handleChange = (e: any) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);

    const handleCreate = () => {
        setFormData(initialFormData);
        setShow(true);
    };

    const handleEdit = (newFormData: any) => {
        setFormData(newFormData);
        setShow(true);
    };

    const MySwal = withReactContent(Swal);

    const handleDelete = () => {
        MySwal.fire({
            title: "Deleted!",
            text: "Your data has been deleted.",
            icon: "success",
        });
    };

    return (
        <MainLayout>
            <ContentHeader title="Company Listing">
                <Button variant="primary" onClick={handleCreate}>
                    <i className="ti ti-plus h5 text-white" />
                </Button>
            </ContentHeader>
            <Row>
                <Table striped>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>Name</th>
                            <th>Country</th>
                            <th>Contact Name</th>
                            <th>Contact Email</th>
                            <th>Is Active</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {listing.map((list: any, index: number) => (
                            <tr key={index}>
                                <td>{index + 1}</td>
                                <td>{list.name}</td>
                                <td>{list.country}</td>
                                <td>{list.contact_name}</td>
                                <td>{list.contact_email}</td>
                                <td>{list.is_active ? "True" : "False"}</td>
                                <td className="d-flex gap-2">
                                    <button
                                        className="btn btn-primary"
                                        onClick={(e) => handleEdit(list)}
                                    >
                                        <i className="ti ti-edit h5 text-white" />
                                    </button>
                                    <button
                                        className="btn btn-danger"
                                        onClick={handleDelete}
                                    >
                                        <i className="ti ti-trash h5 text-white" />
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </Table>
            </Row>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Add Company</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                        <Form.Group className="mb-3" controlId="ControlName">
                            <Form.Label>Name</Form.Label>
                            <Form.Control
                                type="text"
                                name="name"
                                value={formData.name}
                                onChange={handleChange}
                                placeholder="Enter company name ..."
                                autoFocus
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="ControlCountry">
                            <Form.Label>Country</Form.Label>
                            <Form.Control
                                type="text"
                                name="country"
                                value={formData.country}
                                onChange={handleChange}
                                placeholder="Enter country ..."
                            />
                        </Form.Group>
                        <Form.Group
                            className="mb-3"
                            controlId="ControlContactName"
                        >
                            <Form.Label>Contact Name</Form.Label>
                            <Form.Control
                                type="text"
                                name="contact_name"
                                value={formData.contact_name}
                                onChange={handleChange}
                                placeholder="Enter contact name ..."
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="ControlEmail">
                            <Form.Label>Contact Email</Form.Label>
                            <Form.Control
                                type="email"
                                name="contact_email"
                                value={formData.contact_email}
                                onChange={handleChange}
                                placeholder="Enter contact email ..."
                            />
                        </Form.Group>
                        <Form.Group
                            className="mb-3"
                            controlId="ControlIsActive"
                        >
                            <Form.Label>Is Active</Form.Label>
                            <Form.Select
                                name="is_active"
                                defaultValue={String(formData.is_active)}
                                onChange={handleChange}
                            >
                                <option>Select Status</option>
                                <option value="true">True</option>
                                <option value="false">False</option>
                            </Form.Select>
                        </Form.Group>
                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={handleClose}>
                        Save Changes
                    </Button>
                </Modal.Footer>
            </Modal>
        </MainLayout>
    );
}
