import { useState } from "react";
import MainLayout from "../components/layouts/MainLayout";
import ContentHeader from "../components/ContentHeader";
import Row from "react-bootstrap/Row";
import { Table } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Form from "react-bootstrap/Form";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";

export default function Expense() {
    const initialFormData = {
        title: "",
        amount: "",
        date: "",
        comment: "",
        branch: "",
    };

    const listing = [
        {
            title: "Decoration",
            amount: "500",
            date: "2023-01-15",
            comment: "This is some bill which has already been paid",
            branch: "Pune",
        },
        {
            title: "Tea",
            amount: "2000",
            date: "2023-02-15",
            comment: "This is some bill which has already been paid",
            branch: "Ahmedabad",
        },
        {
            title: "Cleaning",
            amount: "1500",
            date: "2023-03-15",
            comment: "This is some bill which has already been paid",
            branch: "Pune",
        },
        {
            title: "Repairs",
            amount: "900",
            date: "2023-04-15",
            comment: "This is some bill which has already been paid",
            branch: "Ahmedabad",
        },
        {
            title: "Utilities",
            amount: "6000",
            date: "2023-05-15",
            comment: "This is some bill which has already been paid",
            branch: "Pune",
        },
        {
            title: "Entertainment",
            amount: "2100",
            date: "2023-06-15",
            comment: "This is some bill which has already been paid",
            branch: "Ahmedabad",
        },
        {
            title: "Equipment",
            amount: "3000",
            date: "2023-07-15",
            comment: "This is some bill which has already been paid",
            branch: "Pune",
        },
    ];

    const [formData, setFormData] = useState(initialFormData);

    const handleChange = (e: any) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);

    const handleCreate = () => {
        setFormData(initialFormData);
        setShow(true);
    };

    const handleEdit = (newFormData: any) => {
        setFormData(newFormData);
        setShow(true);
    };

    const MySwal = withReactContent(Swal);

    const handleDelete = () => {
        MySwal.fire({
            title: "Deleted!",
            text: "Your data has been deleted.",
            icon: "success",
        });
    };

    return (
        <MainLayout>
            <ContentHeader title="Expense Listing">
                <Button variant="primary" onClick={handleCreate}>
                    <i className="ti ti-plus h5 text-white" />
                </Button>
            </ContentHeader>
            <Row>
                <Table striped>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>Title</th>
                            <th>Amount</th>
                            <th>Date</th>
                            <th>Comment</th>
                            <th>Branch</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {listing.map((list: any, index: number) => (
                            <tr key={index}>
                                <td>{index + 1}</td>
                                <td>{list.title}</td>
                                <td>{list.amount}</td>
                                <td>{list.date}</td>
                                <td>{list.comment}</td>
                                <td>{list.branch}</td>
                                <td className="d-flex gap-2">
                                    <button
                                        className="btn btn-primary"
                                        onClick={(e) => handleEdit(list)}
                                    >
                                        <i className="ti ti-edit h5 text-white" />
                                    </button>
                                    <button
                                        className="btn btn-danger"
                                        onClick={handleDelete}
                                    >
                                        <i className="ti ti-trash h5 text-white" />
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </Table>
            </Row>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Add Company</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                        <Form.Group className="mb-3" controlId="ControlTitle">
                            <Form.Label>Name</Form.Label>
                            <Form.Control
                                type="text"
                                name="name"
                                value={formData.title}
                                onChange={handleChange}
                                placeholder="Enter title ..."
                                autoFocus
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="ControlAmount">
                            <Form.Label>Amount</Form.Label>
                            <Form.Control
                                type="text"
                                name="amount"
                                value={formData.amount}
                                onChange={handleChange}
                                placeholder="Enter amount ..."
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="ControlDate">
                            <Form.Label>Date</Form.Label>
                            <Form.Control
                                type="date"
                                name="date"
                                value={formData.date}
                                onChange={handleChange}
                                placeholder="Enter date ..."
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="ControlComment">
                            <Form.Label>Comment</Form.Label>
                            <Form.Control
                                as="textarea"
                                rows={3}
                                name="comment"
                                value={formData.comment}
                                onChange={handleChange}
                                placeholder="Enter comment ..."
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="ControlBrach">
                            <Form.Label>Is Active</Form.Label>
                            <Form.Select
                                name="is_active"
                                defaultValue={formData.branch}
                                onChange={handleChange}
                            >
                                <option>Select Branch</option>
                                <option value="Pune">Pune</option>
                                <option value="Ahmedabad">Ahmedabad</option>
                            </Form.Select>
                        </Form.Group>
                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={handleClose}>
                        Save Changes
                    </Button>
                </Modal.Footer>
            </Modal>
        </MainLayout>
    );
}
