import React, {
    createContext,
    useContext,
    ReactNode,
    useState,
    useEffect,
} from "react";
import axios from "axios";
import { useGoogleLogin, googleLogout, TokenResponse } from "@react-oauth/google";
import { Navigate } from "react-router-dom";

interface User {
    picture: string;
    name: string;
    email: string;
}

interface AuthJson {
    access_token: string;
}

interface AuthContextProps {
    isAuthenticated: boolean;
    user: User | null;
    setIsAuthenticated: React.Dispatch<React.SetStateAction<boolean>>;
    setUser: React.Dispatch<React.SetStateAction<User | null>>;
    logIn: () => any;
    logOut: () => void;
}

const AuthContext = createContext<AuthContextProps | undefined>(undefined);

interface AuthProviderProps {
    children: ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
    const isAuthenticatedBoolean = localStorage.getItem("code-response")
        ? true
        : false;

    const [isAuthenticated, setIsAuthenticated] = useState<boolean>(
        isAuthenticatedBoolean
    );
    const [user, setUser] = useState<User | null>(null);
    const [authJson, setAuthJson] = useState<AuthJson | null>(null);

    useEffect(() => {
        let codeResponse = localStorage.getItem("code-response");

        if (codeResponse) {
            setAuthJson(JSON.parse(codeResponse));
        }
    }, []);

    useEffect(() => {
        if (authJson) {
            axios
                .get(
                    `https://www.googleapis.com/oauth2/v3/userinfo`,
                    {
                        headers: {
                            Authorization: `Bearer ${authJson.access_token}`,
                            Accept: "application/json",
                        },
                    }
                )
                .then((res) => {
                    setIsAuthenticated(true);
                    setUser(res.data);
                })
                .catch((err) => {
                    logOut();
                });
        }
    }, [authJson]);

    const logIn = useGoogleLogin({
        onSuccess: (codeResponse: TokenResponse) => {
            console.log('codeResponse', codeResponse);
 
            // Save to localStorage
            localStorage.setItem("code-response", JSON.stringify(codeResponse));

            setAuthJson(codeResponse);

            <Navigate to="/" replace={true} />;
        },
        onError: (error: any) => console.log("Login Failed:", error),
    });

    const logOut = (): void => {
        googleLogout();
        localStorage.removeItem("code-response");
        setAuthJson(null);
        setUser(null);
        setIsAuthenticated(false);
        <Navigate to="/login" />;
    };

    return (
        <AuthContext.Provider
            value={{
                isAuthenticated,
                user,
                setIsAuthenticated,
                setUser,
                logIn,
                logOut,
            }}
        >
            {children}
        </AuthContext.Provider>
    );
};

export const useAuth = (): AuthContextProps => {
    const context = useContext(AuthContext);
    if (!context) {
        throw new Error("useAuth must be used within an AuthProvider");
    }
    return context;
};
